@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*, *:before, *:after {
  box-sizing: border-box; }

:focus, :active {
  outline: none; }

a:focus, a:active {
  outline: none; }

aside, nav, footer, header, section {
  display: block; }

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  min-width: 320px;
  position: relative;
  width: 100%;
  color: #000; }

body {
  background: #093843;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }
  body.lock {
    overflow: hidden; }
    @media (max-width: 767.98px) {
      body.lock {
        width: 100%;
        position: fixed;
        overflow: hidden; } }

input, button, textarea {
  font-family: "Raleway"; }

input::-ms-clear {
  display: none; }

input{
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

label{
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

button {
  cursor: pointer; 
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a, a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit; 
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}
.wrapper{
  overflow: hidden;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 105px 0;
}
.flex{
  display: flex;
}
.block{
  width: 58.8%;
  max-width: 1129px;
  height: 100%;
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  z-index: 5;
  padding: 76px 80px;
  position: relative;
  overflow: hidden;
}
@media(max-width: 640px){
  .block{
    width: 80%;
    padding: 60px;
  }
}
@media(max-width: 520px){
  .block{
    width: 90%;
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
.title{
  font-family: Rubik;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: #007FC7;
  margin-bottom: 48px;
}

.header{
	font-family: 'Rubik', sans-serif;
	display: flex;
	justify-content: flex-end;
	padding: 28px 39px;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
}
.header__buttons{
	display: inline-flex;
	z-index: 6;
}
.header a{
	border-radius: 30px;
	font-weight: 500;
	font-size: 14px;
	line-height: 17px;
	text-align: center;
	color: #F7F7F7;
	padding: 12px 24px;
	margin-right: 36px;
}
.header a:last-child{
	margin-right: 0;
}
.header__calc{
	background: #CE9912;
}

.header__text{
	background: #FF4A4A;
}

@media(max-width: 350px){
	.header{
		justify-content: space-around;
	}
}
.calculator input{
	border: 1px solid #C4C4C4;
	border-radius: 20px;
	padding: 13px 16px;
	width: 100%;
}
.calculator form{
	width: 100%;
	max-width: 400px;
}
.calculator label{
	display: block;
	margin: 19px 0 0 16px;
}
.calculator button{
	margin-top: 45px;
	background: #007FC7;
	border-radius: 25px;
	font-family: Rubik;
	font-weight: 500;
	font-size: 16px;
	line-height: 19px;
	color: #FFFFFF;
	padding: 10px 28px;
}


.form__input.error input{
	border: 1px solid #FF2C2C;
}
.form__input span{
	display: none;
	color: #FF2C2C;
	margin: 8px 0 0 17px;
}
.form__input.error span{
	display: block;
}


.loading__container{
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(255, 255, 255, 0.5);
}
.loading{
	position: relative;
	border: 9px solid transparent;
	border-top-color: #FF4A4A;
	border-left-color: #FF4A4A;
	width: 100px;
	height: 100px;
	border-radius: 50%;
	-webkit-animation: spin 1s infinite linear;
	        animation: spin 1s infinite linear;
}
.loading:before, .loading:after{
	content: '';
	border: 9px solid transparent;
	position: absolute;
	border-radius: 50%;
}
.loading:before{
	border-top-color: #007FC7;
	border-left-color: #007FC7;
	top: 6px;
	left: 6px;
	width: 70px;
	height: 70px;
	-webkit-animation: spin 1.6s infinite linear;
	        animation: spin 1.6s infinite linear;
}
.loading:after{
	border-top-color: #CE9912;
	border-left-color: #CE9912;
	top: 21px;
	left: 21px;
	width: 40px;
	height: 40px;
	-webkit-animation: spin 2.2s infinite linear;
	        animation: spin 2.2s infinite linear;
}

@-webkit-keyframes spin{
	from{
		transform: rotate(0deg);
	}
	to{
		transform: rotate(360deg);
	}
}

@keyframes spin{
	from{
		transform: rotate(0deg);
	}
	to{
		transform: rotate(360deg);
	}
}
.text__text{
	max-width: 372px;
}
